	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is an API</h2>\n<p>API stands for Application Programming Interface. Don’t worry about the AP, just focus on the I. An API is an interface. You use interfaces all the time. A computer operating system is an interface. Buttons in an elevator are an interface. A gas pedal in a car is an interface.</p>\n<p>An interface sits on top of a complicated system and simplifies certain tasks, a middleman that saves you from needing to know all the details of what’s happening under the hood. A web API is the same sort of thing. It sits on top of a web service, like Twitter or YouTube, and simplifies certain tasks for you. It translates your actions into the technical details for the computer system on the other end.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://schoolofdata.org/2013/11/18/web-apis-for-non-programmers/' target='_blank' rel='nofollow'>API for non-programmers</a></li>\n<li><a href='https://en.wikipedia.org/wiki/Application_programming_interface' target='_blank' rel='nofollow'>Wikipedia</a></li>\n<li><a href='https://medium.freecodecamp.org/what-is-an-api-in-english-please-b880a3214a82' target='_blank' rel='nofollow'>What is an API? In English, please.</a></li>\n</ul>","fields":{"slug":"/computer-science/what-is-an-api/"},"frontmatter":{"title":"What is an API"}}},"pathContext":{"slug":"/computer-science/what-is-an-api/"}}

